import {create} from 'zustand';

export const useGlobalStore = create((set) => ({
    tg_info: {
        chat_id: null,
        logo: null,
        username: null
    },
    setTgInfo: (user) => {
        set((state) => ({
            tg_info: {
                chat_id: user.id, 
                logo: user.photo_url,
                username: `${user.first_name} ${user.last_name}`
            }
        }))
    },
}))